import React, {Component} from 'react';
import fontEnd from "./font-end.png";
import backEnd from'./Back-end.jpg'
import mobile from './mobile.jpg'
import "./Services.css"

class Services extends Component {
    render() {
        return (
            <div>
                <section id="services">
                    <div className="s-heading">
                        <h1>Services</h1>
                        <p>Here Is The Some Services Which We Provide You</p>

                        <div className="s-b-container">
                            <div className="s-box">
                                <div className="s-b-img">
                                    <div className="s-type">Front-End</div>
                                    <img src={fontEnd}></img></div>

                                <div className="s-b-text">
                                    <a href="#">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when ...</a></div>
                            </div>
                            <div className="s-box">
                                <div className="s-b-img">
                                    <div className="s-type">Back-End</div>
                                    <img src={backEnd}></img></div>
                                <div className="s-b-text">
                                    <a href="#">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when ...</a></div>
                            </div>
                            <div className="s-box">
                                <div className="s-b-img">
                                    <div className="s-type">React Native</div>
                                    <img src={mobile}></img></div>
                                <div className="s-b-text">
                                    <a href="#">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when ...</a></div>
                            </div>
                        </div>
                    </div>


                </section>
            </div>
        );
    }
}

export default Services;