import React, {Component} from 'react';
import "./Contract.css"
import emailjs from 'emailjs-com'
class Contract extends Component {
    sendEmail=(e)=>{
        e.preventDefault();

        emailjs.sendForm('gmailportfolio', 'template_o582ndg', e.target, 'user_ms5ma3h5keiRP0i9LNxE9')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        e.target.reset()
    }
    render() {
        return (
            <div>

                <section id="contact-btn">
                    <h1>If You Have Any Project In Your Mind ?</h1>
                    <a href="#contact-form">Contract Me</a>

                </section>
                <section id="contact-form">'
                    <form onSubmit={this.sendEmail}>
                        <div className="contact-left">
                            <h1 className="c-l-heading"><font style={{
                                borderBottom:"3px solid #1ed98b"
                            }}>Write</font> Us</h1>
                            <div className="f-name">
                                <font>Name</font>
                            <input type="text"  name="name" placeholder="Full Name"/>

                        </div>

                            <div className="f-email">
                                <font>Email</font>
                                <input type="email" name="email" placeholder="Example@hmail.com"/>

                            </div>

                        </div>
                        <div className="contact-right">
                            <div className="message">
                                <font>Message</font>
                                <textarea name="message" row="5" cols="20" placeholder="Write Message..."/>
                            </div>
                            <button >Submit</button>

                        </div>

                    </form>

                </section>
            </div>
        );
    }
}

export default Contract;