import React, {Component} from 'react';
import "./Portfolio.css"
import p1 from './project-1.jpg'
import p2 from './project-2.jpg'

class Portfolio extends Component {
    render() {
        return (
            <div>
                <section id='portfolio'>
                    <h1 className="p-heading">Portfolio</h1>
                    <div className="p-b-container">
                        <div className="p-box">
                            <div className="text-overlay">
                                <h1>COVID-19 SURVEY FORM</h1>
                                <p>Client Project</p>
                            </div>
                            <img src={p1} alt="portfolio"/>
                        </div>
                        <div className="p-box">
                            <div className="text-overlay">
                                <h1>COVID-19 LIVE STATS</h1>
                                <p>Client Project</p>
                            </div>
                            <img src={p2} alt="portfolio"/>
                        </div>
                        <div className="p-box">
                            <div className="text-overlay">
                                <h1>COVID-19 SURVEY FORM</h1>
                                <p>Client Project</p>
                            </div>
                            <img src={p1} alt="portfolio"/>
                        </div>

                    </div>
                </section>
            </div>
        );
    }
}

export default Portfolio;