import React, {Component} from 'react';
import './About.css'
import image from "../Home Page/About.jpg";

class About extends Component {
    render() {
        return (
            <div>
                <section id="about">
                    <div className="about-text">
                        <h1>About us</h1>
                        <h2>Freelancing FrontEnd Developer</h2>
                        <p>
                            I Will Design Your Web Site Whatever You Want.

                        </p>
                        <button><a href="https://www.facebook.com/abdulkadir.shohan.5" target="_blank">More Details</a></button>
                    </div>
                    <div className="about-model">
                        <img src={image} alt="model"/>
                    </div>
                </section>
            </div>
        );
    }
}

export default About;